.componentSale {
    letter-spacing: 1px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0px 200px;
}

.product{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0px 200px;
}
.component img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 3px;
    max-width: 285px;
    max-height: 350px;
    transition: all 0.5s;
    min-height: 350px;
   
}
.component img:hover {
    transform: scale(1.05);
}
.image{
    overflow: hidden;
    max-width: 285px;
    max-height: 350px;
}
.component {
    max-width: 285px;
    max-height: 350px;
    margin:  60px 10px;
    margin-bottom:220px;
    justify-content: center;
    align-items: center;
}
.productId{
      display: flex;
    justify-content: center;
    text-align: center;
    margin: 20px 3px 20px;
    font-family: 'Montserrat', sans-serif;
     font-size: 19px;
     font-weight: 540;
     height: 50px;
}
.size{
    display: flex;
    justify-content: center;

}
.size button{
    margin: 3px;
    border-radius: 3px;
    
}
.size button:hover {
    color: #bf1650;
    background-color: #eaeaead2;
  }
  




.price{
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 19px;
    font-weight: 400;
    margin-top: 15px;
    color: red;
    margin-bottom: 20px;
}
.price p{
    color:black;
}
.price span {
    margin-left: 10px;
    font-weight: 200;
    color: #999999;
    text-decoration: line-through;
}



.favorites{
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    display: flex;
     margin: -430px 240px 400px;
}
.favorite{
    z-index: 1;
    justify-content: center;
    align-items: center;
    display: flex;
    width: 30px;
    height: 30px;
    border-radius:15px;
    background-color: white;
    
}
.favorite:active{
    color: red;
    transition: 0s;
    z-index: 1;
}
.favorite:hover{
    
    color: red;
    transition: 0s;
    z-index: 1;
}

.addToCart {
    display: flex;
    justify-content: center;
    width: 100%;
}


.addToCart button{
    margin-bottom: 400px;
}

.addToCart span button{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 19px;
    max-height: 100%;
    max-width: 100%;
        padding: 5px;
    border-radius: 5px;
    letter-spacing: 1px;
    transform-style: preserve-3d;
    transform: translateZ(-25px);
    transition: transform .25s;
    font-family: 'Montserrat', sans-serif;
    
  }
  .addToCart span button:before,
  .addToCart span button:after{
    content: "Add to Cart";
    height: 108%;
    width: 104%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 3px solid black;
    box-sizing: border-box;
    border-radius: 5px;
  }
  .addToCart span button:before{
    color: #fff;
    background: #000;
    transform: rotateY(0deg) translateZ(25px);
  }
  .addToCart span button:after{
    color: #000;
    transform: rotateX(90deg) translateZ(25px);
  }
  .addToCart span button:hover{
      
    transform: translateZ(-25px) rotateX(-90deg);
  }
  

@media all and (max-width: 500px){
    .componentSale {
        letter-spacing: 1px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: 0px auto;
    }
    
    .product{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: 0px auto;
    }
    .component img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 3px;
        max-width: 285px;
        max-height: 350px;
        transition: all 0.5s;
        min-height: 350px;
       
    }
}
  